import { Inline, Action } from '@ali/4ever-cangjie';

export interface ActivePlaceholderAction {
  type: typeof MENTION_PLACEHOLDER_ACTIVE;
  payload: {
    node: Inline;
  }
}

export const MENTION_PLACEHOLDER_ACTIVE = Symbol('MENTION_PLACEHOLDER_ACTIVE');

export function activePlaceholder(node: Inline): ActivePlaceholderAction {
  return {
    type: MENTION_PLACEHOLDER_ACTIVE,
    payload: {
      node
    },
  };
}

export function isActivePlaceholder(action: Action): action is ActivePlaceholderAction {
  return action.type === MENTION_PLACEHOLDER_ACTIVE;
}
